<template>
  <div id="Attendance">
    <div class="mw">
      <!--  begin -->
      <div class="left">
        <div class="endSign">
          <i class="iconfont" @click="endSign" title="">&#xe7d1;</i>
        </div>
        <div class="box">
          <div class="hd">
            <h4>Tip:APP</h4>
            <h3>{{count}}</h3>
          </div>
          <div class="bd">
            <img src="../png/cust.png" alt="">
            <div class="table">
              <div class="progress-bar">
                <div id='loader'>
                  <div class='clip' id="loaded">
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- begin-->
          <div class="popIn block" v-show="ShowBlock">
            <div class="block_header">
              <p>Tip:</p>
            </div>

            <div class="block_body">
              <!---->
              <img src="../png/home.gif" alt="" class="code">
              <p></p>
            </div>
          </div>
          <!-- end-->
        </div>
      </div>
      <!-- end-->
      <!-- begin-->
      <div class="right">
        <div class="main">
          <div class="main-header">
            <h3></h3>
            <div class="menu bounce1" v-show="isMenu" id="bounce">
              <ul>
                <li @click="toData()" v-show="!ifSign" :class="{'noLogin': !isLogin,'menu-main': true}">
                  <i class="iconfont">&#xe61b;</i>
                  <span></span>
                </li>
                <li class="spacer" v-show="!ifSign"></li>
                <li class="menu-main" @click="getWorks('test1')" v-show="!ifSign">
                  <i class="iconfont">&#xe633;</i>
                  <span></span>
                </li>
                <li class="spacer" v-show="!ifSign"></li>
                <li class="menu-main" @click="showLate(classIndex, studentId)" v-show="ifSign">
                  <i class="iconfont" v-if="ifLate">&#xe6f2;</i>
                  <span></span>
                </li>
                <li class="spacer" v-show="ifSign"></li>
                <li class="menu-main" @click="showTruancy(classIndex, studentId)" v-show="ifSign">
                  <i class="iconfont" v-if="ifTruancy">&#xe6f2;</i>
                  <span></span>
                </li>
                <li class="spacer" v-show="ifSign"></li>
                <li class="menu-main" @click="setSign(classIndex, studentId)">
                  <i class="iconfont">&#xe67a;</i>
                  <span></span>
                </li>

              </ul>
            </div>
          </div>
          <div class="spacer"></div>
          <!-- main-body begin -->
          <div class="main-body" ref="main">
            <div class="box">
              <!---->
              <div v-for="(value, index1) in classMsg">
                <!---->
                <div class="class_head">{{classMsg[index1].class_id}}</div>
                <div class="class_body">
                  <!---->
                  <div class="line" v-for="(value,index2) in classMsg[index1].students">
                    <div :class="{ person_box: classMsg[index1].students[index2].checkBtn}">
                      <!---->
                      <div class="person" @click.stop="setIndex(index1,index2)" title=""
                           :class="classMsg[index1].students[index2].ifSign ? 'isSign' : 'notSign'">{{index2 + 1}}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!---->
    <!--<transition name="warning">-->
    <!--<div class="onLoad" v-if="isLoading"><span>{{ warning }}</span></div>-->
    <!--</transition>-->

    <transition name="picture">
      <div class="picture" v-show="showPic">
        <div class="pic-bg"></div>
        <div class="pic-wrap">
          <i class="pic-del iconfont" title="" @click="delAlert">&#xe620;</i>
          <span class="pic-title"></span>
          <img :src="imgURL"/>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  // import {works} from "../axios/api";

  export default {
    name: "Attendance",
    data() {
      return {
        count: '',

        // 
        classMsg: [],

        // 
        showPic: false,

        // 
        imgURL: '',

        //
        classIndex: '',

        //
        studentId: '',

        //
        ifSign: true,
        ifLate: true,
        ifTruancy: true,

        //
        isMenu: false,

        // 
        loader_width: 300,    // 
        loader_height: 8,    // 
        loaded_width: 0,      // 
        loaded_height: 8,    // 
        loader_speed: 10000,  // (ms)
        time: '',

        checkBtn: true,

        ani_status: 0,
      }
    },
    computed: {
      ...mapState([
        'isLoading',
        'warning',
        'EndSign',
        'ShowBlock',
        'Class_lists',
        'isLogin',
        'To_Data',
        'full_screen',
        'full',
      ])
    },
    created() {

      // 
      console.log('', this.Class_lists);

      //-------->if
      if (this.Class_lists.length === 0) {
        this.$store.commit('change', JSON.parse(localStorage.getItem('class_lists')));
      }

      //classMsg----------------
      this.classMsg = this.Class_lists;
      console.log('classMsg', this.classMsg);

      //
      // this.set_time();

      //
      this.forClock();

      // 
      this.processBar();
    },
    mounted() {
      // 
      var loader = document.getElementById("loader"); // 
      var loaded = document.getElementById("loaded"); // 
      // 
      loader.style.width = this.loader_width + 'px';  // 
      loader.style.height = this.loader_height + 'px'; // 
      loaded.style.width = this.loaded_width + 'px';  // 
      loaded.style.height = this.loaded_height + 'px'; // 
      console.log(":", loader.style.width);
    },
    destroyed() {
      clearTimeout(this.time);
    },
    methods: {
      //
      clock() {
        const TIME_COUNT = 10;
        if (!this.timer) {
          this.count = TIME_COUNT;
          // this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 1 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              clearInterval(this.timer);
              this.timer = null;
              //
              this.count = 10;
              this.forClock()
            }
          }, 1000)
        }
      },
      // 
      clearCheckBtn() {
        for (var i = 0; i < this.classMsg.length; i++) {
          for (var j = 0; j < this.classMsg[i].students.length; j++)
            this.classMsg[i].students[j].checkBtn = false;
        }
      },
      // 
      delAlert() {
        // 
        this.showPic = false;
      },
      endSign() {
        if (this.ShowBlock === false) {
          this.setAttention("", {EndSign: true});
          // this.$store.commit("")
        } else {
          this.setWarning('');
        }
      },
      //60
      forClock() {
        for (let i = 1; i < 60; i++) {
          this.clock();
        }
      },
      // 
      processBar() {
        var count = (this.loader_width * 20) / this.loader_speed; // px
        var total = 0; // 
        var width = this.loader_width;
        this.time = setInterval(function () { // 
          total += count;
          loaded.style.width = total + 'px';
          var d_width = loaded.style.width;
          if (parseInt(d_width) >= width) {
            var myDate = new Date(); // 
            console.log("", myDate.toLocaleString());
            console.log("");
            total = 0;
            loaded.style.width = total + 'px';
          }
        }, 20);
      },
      ////
      set_time() {
        //
        get_code().then(result => {
          //  push
        }).catch(error => {
          console.log(error.response)
        });
        //60
        let stry = '012345678901234567890123456789012345678901234567890123456789';
        for (let j = 0; j < stry.length; j++) {
          (function () {
            let t = j + 1;
            let tyy = stry[j];
            setTimeout(function () {
              console.log(tyy + 'code');
              // 
              get_code().then(result => {
                //  push
              }).catch(error => {
                console.log(error.response)
              })
            }, 10 * 1000 * t)
          })()
        }
      },

      setIndex(index1, index2) {
        var student = this.classMsg[index1].students[index2];
        this.clearCheckBtn(index1, index2);
        this.showMenu(index1, index2);
        this.classIndex = index1;
        this.studentId = index2;
        this.ifSign = !student.ifSign;
        this.ifLate = student.Late;
        this.ifTruancy = student.Truancy;
      },

      showMenu(index1, index2) {
        var student = this.classMsg[index1].students[index2];
        if (this.classIndex === index1 && this.studentId === index2 && this.isMenu === true) {
          this.isMenu = false;
          student.checkBtn = false;
        } else {
          this.isMenu = true;
          student.checkBtn = true;
          this.setAnimation();
        }
      },
      setAnimation() {
        var ani_icon = document.getElementById('bounce');
        if (this.ani_status === 1) {
          ani_icon.className = 'menu bounce1';
          this.ani_status = 2;
        } else {
          ani_icon.className = 'menu bounce2';
          this.ani_status = 1;
        }
      },
      // 
      setAttention(msg,obj){
        this.$store.commit('SET_ATTENTION', {
          ifAlert: true,  // 
          at_warning: msg, // 
          EndSign: obj.EndSign,
          noLogin: obj.noLogin,
          To_Data: obj.To_Data,
        });
      },
      // 
      setWarning(msg){
        this.$store.commit('SET_LOADING', {isLoading: true, warning: msg});
        setTimeout(() => {
          this.$store.commit('SET_LOADING', false);
        }, 1000);
      },
      // 
      setSign(index1, index2) {
        var classroom = this.classMsg[index1];
        var student = this.classMsg[index1].students[index2];
        this.setWarning('');
        student.ifSign = !student.ifSign;
        student.ifSign === true ? classroom.noSign -= 1 : classroom.noSign += 1;
        this.ifSign = !student.ifSign;
        // 
        localStorage.setItem('class_lists', JSON.stringify(this.classMsg));
      },


      // //
      set_time() {
        //120
        let stry = '012345678901234567890' +
          '123456789012345678901234567890' +
          '123456789012345678901234567890' +
          '123456789012345678901234567890123456789';
        for (let j = 0; j < stry.length; j++) {
          (function () {
            let t = j + 1;
            let tyy = stry[j];
            setTimeout(function () {
              console.log(tyy + "stu");
              get_stu().then(result => {
                //index1/2 
                this.classMsg[index1].student[index2].push({isSign: true})
              }).catch(error => {
                console.log(error.response)
              })
            }, 5 * 1000 * t)
          })()
        }
      },

      showLate(index1, index2) {
        const student = this.Class_lists[index1].students[index2];
        this.setWarning('');
        if (student.Late === true) {
          this.setWarning('');
        } else {
          student.Late = true;
          this.ifLate = true;
          student.Truancy = false;
          this.ifTruancy = false;
        }
      },

      showTruancy(index1, index2) {
        console.log("");
        const student = this.Class_lists[index1].students[index2];
        this.setWarning('');
        if (this.Class_lists[index1].students[index2].Late === false) {
          this.setWarning('');
        } else {
          student.Late = false;
          student.Truancy = true;
          this.ifLate = false;
          this.ifTruancy = true;
        }
      },
      // 
      toData() {
        this.isLogin ?
          this.$router.push({name: 'data'}) :
          this.setAttention('',{noLogin: true,To_Data: true});
      },
    },
  }
</script>

<style scoped>
  @import "../css/css3.css";

  .mw {
    margin: 2px auto;
    width: 1400px;
    height: 600px;
    position: relative;
  }

  .left .box {
    margin-left: -50px;
  }

  .left {
    width: 535px;
    height: 650px;
    float: left;
  }

  .endSign {
    height: 100px;
  }

  .endSign i {
    float: right;
    padding: 10px;
    font-size: 30px;
    color: #8d8a8a;
    cursor: pointer;
  }

  .hd {
    text-align: center;
  }

  .hd h4 {
    height: 60px;
  }

  .hd h3 {
    font-weight: 700;
    font-size: 18px;
    height: 60px;
  }

  .bd {
    text-align: center;
  }

  .bd img {
    height: 350px;
    width: 350px;
    border: #e7e9ef 3px solid;
  }

  .table {
    margin-top: 20px;
  }

  #loader {
    border-radius: 30px;
    background-color: #f9f7f7;
    border: 1px solid #f1f0f0;
    padding: 0px 2px;
    display: inline-block;
    border-left: 1px solid #e7e9ef;
    border-top: 2px solid #e7e9ef;
    border-right: 1px solid #e7e9ef;
    border-bottom: 2px solid #e7e9ef;
  }

  .block .block_header p {
    font-size: 20px;
    margin-top: 10vh;
    margin-left: 60px;
  }

  .block_body {
    margin-top: 20vh;
    margin-left: 60px;
  }

  .popIn {
    position: absolute;
    top: 50px;
