    height: 100%;
    width: 535px;
    text-align: center;
  }

  .right {
    margin-top: 38px;
    float: right;
    width: 850px;
    height: 600px;
    background-color: #fff;
    overflow: hidden;
  }

  .main {

    margin: 30px auto;
    width: 700px;
  }

  .main-header {
    height: 40px;
    line-height: 40px;
    background-color: #F6F8F9;
    border: 1px solid #c9cace;
    margin-bottom: 5px;
  }

  .main-header h3 {
    margin-left: 10px;
    display: inline-block;
    vertical-align: top;
    font-size: 16px;
    font-weight: 700;
    color: #1aad19;
  }

  .main-header .menu {
    float: right;
    margin-right: 20px;
    display: inline-block;
    vertical-align: top;
  }

  .main-header .menu .iconfont {
    font-size: 12px;
  }

  .main-header .menu ul {
    margin-top: 10px;
  }

  .main-header .menu li {
    float: right;
    cursor: pointer;
  }

  .main-header .menu li:hover {
    color: #1aad19;
  }

  .main-header .menu .spacer {
    height: 15px;
    width: 1px;
    background-color: #aaa;
    margin-top: 3px;
  }

  .main-header .menu .menu-main {
    font-size: 12px;
    line-height: 20px;
    padding: 0 15px;
    margin: 0 3px;
  }

  /**/
  .right .main-body {
    height: 500px;
    overflow-y: auto;
  }

  /**/
  .main-body .box {
    border: 1px solid #c9cace;
  }

  /**/
  .class_head {
    background-color: #F6F8F9;
    height: 25px;
    line-height: 25px;
    font-weight: 700;
    padding-left: 18px;
    margin-bottom: 10px;
  }

  /**/
  .class_body {
    width: 675px;
  }

  /**/
  .line {
    text-align: center;
    display: inline-block;
    width: 10%;
    height: 30px;
    font-size: 14px;
    border-bottom: #e7e7eb solid 1px;
    border-top: #e7e7eb solid 1px;
    margin-bottom: 10px;
  }

  .person_box {
    border: 1px solid #96c2f1;
    background: #eff7ff;
    margin: -1px;
  }

  /**/
  .line .person {
    height: 30px;
    line-height: 30px;
    width: 30px;
    display: inline-block;
    border-radius: 15px;
