<template>
  <div id="navVue">
    <!---->

    <div class="nav">
      <div class="w clearFix">
        <!-- left begin-->
        <ul class="fl clearFix">
          <li id="logo" @click="toHome">
            <img src="../png/LOGO2.png" alt="">
            <span></span>
          </li>
          <li :class="{ currentPage : this.$route.name === 'home'}" @click="toHome(true)">
            <i class="iconfont img img-home">&#xe639;</i>
            
          </li>
          <li :class="{ currentPage: this.$route.name === 'attendance'}" @click="toAttendance">
            <i class="iconfont img img-attendance">&#xe605;</i>
            
          </li>
          <li :class="isLogin ? {currentPage: this.$route.name === 'data', img_data: true} : 'noLogin'" @click="toData">
            <i class="iconfont img img-data">&#xe61b;</i>
            
          </li>
        </ul>
        <!-- left end-->
        <!-- right begin -->
        <ul class="fr">
          <li class="dd"><!--dropdown-->
            <div class="set">
              <i class="iconfont img">&#xe604;</i>
              
              <i class="dd-icon"></i>
            </div>
            <div class="dd-m"><!--dropdown-menu-->
              <div class="dd-m1" @click="toIntro">
                <i class="iconfont img">&#xe66a;</i>
                
              </div>
              <div class="dd-m2" @click="del">
                <i class="iconfont img">&#xe600;</i>
                
              </div>
            </div>
          </li>
          <li class="spacer"></li>
          <li class="log login" v-if="!isLogin">
            <a href="#" @click="toLogin()"></a>
          </li>
          <li class="log logout" v-if="isLogin">
            <a href="#" @click="toLogout"></a>
          </li>
        </ul>
        <!-- right begin-->
      </div>

    </div><!--nav-->

    <!---->
    <transition name="alert">
      <div class="alert" v-show="ifAlert">
        <div class="alert-bg"></div>
        <div class="alert-wrap">
          <i class="alert-del iconfont" title="" @click="alertDel">&#xe620;</i>
          <span class="alert-title"></span>
          <div class="alert-tip">{{ at_warning }}</div>

          <!---->
          <div class="alert-button" v-show="noLogin">
            <button class="alert-toLogin" @click="toData_Login"></button>
            <button class="alert-cancel" @click="alertDel"></button>
          </div>

          <!---->
          <div class="alert-button" v-show="newSign">
            <button class="alert-toLogin" @click="toHome(false)"></button>
            <button class="alert-cancel" @click="alertDel"></button>
          </div>

          <!---->
          <div class="alert-button" v-show="EndSign">
            <button class="alert-toLogin" @click="showBlock(true)"></button>
            <button class="alert-cancel" @click="alertDel"></button>
          </div>

          <!--F11-->
          <div class="alert-button" v-show="full_screen">
            <button class="alert-toLogin" @click="fullScreen"></button>
            <button class="alert-cancel" @click="alertDel"></button>
          </div>
        </div>
      </div>
    </transition>

    <!---->
    <transition name="warning">
      <div class="onLoad" v-if="isLoading === true"><span>{{ warning }}</span></div>
    </transition>

    <transition name="introduction">
      <div class="intro" v-show="showIntro">
        <div class="intro-bg"></div>
        <div class="intro-wrap">
          <i class="intro-del iconfont" title="" @click="alertDel">&#xe620;</i>
          <span class="intro-title"></span>
          <div class="content">
            <p>
              <br>
              
            </p>
            <br>
            <p class="text1"><br>
              <br>
              <br>

              
            </p>
            <br>
            <p>
              
            </p><br>
            <img src="../png/intro/1.png" alt="">
            <br>
            <br>
            <p><br>
              <br>
              <br>
              
            </p><br>
            <img src="../png/intro/2.png" alt="">
            <br><br>
            <p class="text1">
               <br>
               <br>
            </p>
            <p></p><br>
            <img src="../png/intro/3.png" alt="">
            <img src="../png/intro/4.png" alt="">
            <br><br>
            <p></p><br>
            <img src="../png/intro/5.png" alt="">
            <br><br>
            <p> <br>
              <br>
              
            </p><br>
            <img src="../png/intro/6.png" alt="">
            <img src="../png/intro/7.png" alt="">
            <br>
            <br>
            <p class="text1"></p> <br>
            <p></p><br>
            <img src="../png/intro/8.png" alt="">
            <br><br>
            <p></p>
            <br>
            <img src="../png/intro/9.png" alt="">
            <img src="../png/intro/10.png" alt="">
          </div>
        </div>
      </div>
    </transition>

  </div>
</template>

<script>
  import {mapState} from 'vuex'
  // import {} from "../axios/api";


  export default {
    name: "navVue",
    data() {
      return {
        // 
        currentPage: false,

        showIntro: false,
      }
    },
    computed: {
      ...mapState([
        'Class_lists',
        'isLoading',
        'isLogin',
        'warning',
        'at_warning',
        'ifAlert',
        'ifShow_login_alert',
        'ifShow_reg_alert',
        'noLogin',
        'newSign',
        'EndSign',
        'full_screen',
        'to_data',
        'ShowBlock',
        'full',
      ])
    },
    methods: {

      fullScreen() {
        this.alertDel();
        this.$store.commit('SET_FULL', true);
        var el = document.getElementById('app');
        var rfs = el.requestFullScreen || el.webkitRequestFullScreen || el.mozRequestFullScreen || el.msRequestFullScreen,
          wscript;

        if (typeof rfs != "undefined" && rfs) {
          rfs.call(el);
          return;
        }

        if (typeof window.ActiveXObject != "undefined") {
          wscript = new ActiveXObject("WScript.Shell");
          if (wscript) {
            wscript.SendKeys("{F11}");
          }
        }

      },

      // 
      toHome(bool) {
        console.log(bool);
        if (bool) {
          if (this.$route.name === 'attendance') {
            if (this.ShowBlock === false) {
              // 
              this.$store.commit('SET_ATTENTION', {
                ifAlert: true,  // 
                at_warning: '', // 
                noLogin: false, // 
                newSign: true,  // 
              });
            } else {
              // 
              this.$store.commit('SET_ATTENTION', {
                ifAlert: true,  // 
                at_warning: '', // 
                noLogin: false, // 
                newSign: true,  // 
              });
            }
          } else {
            this.$router.push({name: 'home'});
          }
        } else {
          this.$store.commit('SET_ATTENTION', false);
          this.$store.commit('change', '');
          this.$store.commit('SHOW_BLOCK', false);
          this.$router.push({name: 'home'});
        }

      },

      // 
      toAttendance() {
        console.log(this.Class_lists.length);
        if (this.Class_lists.length === 0) {

          this.$store.commit('SET_LOADING', {isLoading: true, warning: '""'});
          setTimeout(() => {
            this.$store.commit('SET_LOADING', false);
          }, 1000);

          this.$router.push({name: 'home'})
        } else {
          this.$router.push({name: 'attendance'});
        }
      },

      // 
      toData() {
        console.log("", this.isLogin);
        if (this.isLogin === true) {
          console.log("");
          console.log("");
          if (this.Class_lists.length === 0) {

            console.log("");
            console.log("");
            this.$store.commit('SET_LOADING', {isLoading: true, warning: ''});
            setTimeout(() => {
              this.$store.commit('SET_LOADING', false);
            }, 1000);
            console.log("");
            this.$router.push({name: 'home'})
          } else {
            console.log("");
            console.log("");
            this.$router.push({name: 'data'});
          }
        } else {
          console.log("");
          console.log("");
          this.$store.commit('SET_ATTENTION', {
            ifAlert: true,
            at_warning: '',
            noLogin: true,
            newSign: false,
            to_data: true,
          });
        }
      },

      // 
      toIntro() {
        // this.$store.commit('SET_LOADING', {isLoading: true, warning: ''});
        // setTimeout(() => {
        //   this.$store.commit('SET_LOADING', false);
        // }, 1000);
        //   console.log("123");
        // }, 1000);
        // this.$router.push({name: 'introduction'});
        this.showIntro = true;
      },

      // 
      // toMang(){
      //   this.$store.commit('SET_LOADING', {isLoading: true, warning: ''});
      //   setTimeout(() => {
      //     this.$store.commit('SET_LOADING', false);
      //   }, 1000);
      // },

      // 
      del() {
        this.$store.commit('SET_LOADING', {isLoading: true, warning: ','});
        setTimeout(() => {
          this.$store.commit('SET_LOADING', false);
        }, 1000);

        this.$router.push({name: 'home'});
        // 
        window.onbeforeunload = function (e) {
          var storage = window.localStorage;
          storage.clear();
        };
        // 
        window.location.reload();
      },

      // 
      alertDel() {
        // 
        this.$store.commit('SET_ATTENTION', false);
        this.showIntro = false;
      },

      // 
      toLogin() {
        this.$store.commit('SET_ATTENTION', false);
        this.$store.commit('SHOW_LOGIN', true);
        console.log('', this.ifShow_login_alert);
      },

      // 
      toData_Login() {
        console.log("");
        this.$store.commit('SET_ATTENTION', {ifAlert: false, To_Data: true});
        // this.$store.commit('TO_DATA', true);
        this.$store.commit('SHOW_LOGIN', true);
        console.log('', this.ifShow_login_alert);
      },

      // 
      toReg() {
        this.$router.push({name: 'register'});
      },

      checkbox() {
        console.log("");
      },

      // 
      toLogout() {
        this.$store.commit('SET_LOADING', {isLoading: true, warning: ''});
        setTimeout(() => {
          this.$store.commit('SET_LOADING', false);
        }, 1000);
        if (this.$route.name !== 'home') {
          this.$router.push({name: 'home'});
        }
        this.$store.commit('SET_LOGIN', false);
      },

      showBlock() {
        this.$store.commit('SHOW_BLOCK', true);
        this.$store.commit('SET_ATTENTION', false);
      }
    }
  }
</script>

<style scoped>
