    border-top: #1aad19 solid 2px;
    box-shadow: 0 2px 4px 0 rgba(238, 238, 238, 0.5);
    font-size: 16px;
  }

  .fl {
    float: left;
  }

  .nav li {
    position: relative;
    float: left;
    cursor: pointer;
  }

  .nav .fl li {
    margin-left: 80px;
    height: 60px;
  }

  .fr {
    float: right;
  }

  i {
    display: inline-block;
    vertical-align: top;
  }

  /**/
  .currentPage {
    border-bottom: #1aad19 solid 2px;
    background-color: #edeef0;
    padding: 0 10px;
