    height: 30px;
    background-color: #ccc;
    margin: 15px 15px 0;
  }

  /**/
  .dd:hover .dd-m div {
    opacity: 1;
    transform: none;
  }

  .dd-m div:nth-child(1) {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
