  }

  .dd-m div:nth-child(2) {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    -webkit-transition-delay: 200ms;
  }

  .dd:hover .dd-m div:nth-child(1) {
    -webkit-transition-delay: 0s;
  }

  .dd:hover .dd-m div:nth-child(2) {
    -webkit-transition-delay: 50ms;
  }

  .dd-m {
    display: block;
    z-index: 999;
    position: absolute;
    margin-top: -10px;
    left: -20px;
    width: 120px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    -webkit-perspective: 400px;
    max-height: 0;
  }

  .dd-m div {
    position: relative;
    height: 40px;
    line-height: 40px;
    background-color: #fff;
    text-align: center;
    opacity: 0;
    transform: rotateY(90deg);
    transition: opacity .4s, transform .5s;
  }

  /**/

  .dd-m div:hover {
    background-color: #edeef0;
  }

  .img-set {
    background: url(../png/set.svg);
    -webkit-background-size: 100%;
    background-size: 100%;
    left: 0;
  }

  .img-set {
    background: url(../png/set.svg);
    -webkit-background-size: 100%;
    background-size: 100%;
    left: 0;
  }

  .dd-m {
    font-size: 14px;
  }

  .dd-m1 i {
    font-size: 20px;
  }


  .fr log a:active {
    color: #1aad19;
  }

  /*  right end */


  /*====================    nav-end     ================*/


  /*====================    alert-begin     ================*/
  .alert {
    /*display: none;*/
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 99;
  }

  .alert-del {
    cursor: pointer;
    float: right;
    padding: 4px;
    font-size: 30px;
    color: #8d8a8a;
  }

  .alert button {
    border: none;
    /*background: none;*/
  }

  .alert-bg {
    width: 100%;
    height: 100%;
    background: #000;
    filter: alpha(opacity=50);
    opacity: .5;
  }

  .alert-wrap {
    position: absolute;
    max-width: 420px;
    height: 200px;
    left: 50%;
    top: 25%;
