<template>
  <div id="app">

    <div class="sign-box w">
      <h4>Tip:+</h4>
      <button class="sign-bt" @click="toAttendance"></button>
      <div class="change">
        <i class="iconfont" @click="addList" title="">&#xe66b;</i>
        <i class="iconfont" @click="reduceList()" title="">&#xe603;</i>
      </div>
      <div class="msg">
        <ul>
          <li v-for="list in lists">
            <input type="number" v-model="list.class_id" placeholder="">
            <input type="number" v-model="list.firstStu_id" placeholder="" @change="saveList()"
                   @keyup.enter="toAttendance"> -
            <input type="number" v-model="list.lastStu_id" placeholder="" @change="saveList()"
                   @keyup.enter="toAttendance">
          </li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script>

  import {postList} from '../axios/api'
  import {mapState} from 'vuex'

  export default {
    name: 'app',
    data() {
      return {
        lists: [],

        class_lists: [],
        post_list: []
      }
    },
    created() {
      let lists = JSON.parse(localStorage.getItem('lists'));
      console.log('created:',lists);
      if (lists) {
        this.lists = lists;
      } else {
        this.addList();
      }
    },
    methods: {
      // 
      saveList() {
        localStorage.setItem("lists", JSON.stringify(this.lists));
      },

      restoreList() {
        return localStorage.getItem('lists');
      },

      // 
      addList() {
        this.lists.push({class_id: '', firstStu_id: '1', lastStu_id: '', total: ''});
        this.saveList();
      },

      // 
      reduceList(){
        if (this.lists.length === 1)
          alert("");
        else {
          this.lists.splice(this.lists.length - 1, 1);
          this.saveList();
        }
      },

      // 
      toAttendance() {
        // 
        console.log(':', this.lists.length);
        for (let i = 0; i < this.lists.length; i++) {

          if (this.lists[i].firstStu_id === '' || this.lists[i].lastStu_id === '' , this.lists[i].class_id === '') {
            this.$store.commit('SET_LOADING', {isLoading: true, warning: ''});
            setTimeout(() => {
              this.$store.commit('SET_LOADING', false);
            }, 1000);
            return 0;
          }

          // push
          const obj = {students: []};
          const list = this.lists[i];

          // 
          obj.class_id = list.class_id;

          // 
          obj.total = list.lastStu_id - list.firstStu_id + 1;

          // 
          obj.noSign = obj.total;

          // 
          for (var j = 0; j < obj.total; j++) {
            obj.students.push({id: j + 1, isSign: false, Late: true, Truancy: false});
          }

          // push
          this.class_lists.push(obj);
          console.log(obj.class_id, ':', obj);
        }

        // 
        console.log('class_list:', this.class_lists);
        this.$store.commit('change', this.class_lists);

        // 
        localStorage.setItem("class_lists", JSON.stringify(this.class_lists));

        // 
        this.saveList()
        console.log('', JSON.parse(localStorage.getItem('lists')));

        //post
        this.getList();

        // 
        this.$router.push({name: 'attendance'});
      },

      //post
      getList() {

        let a = {};
        //
        for (let i = 0; i < this.class_lists.length; i++) {
          let gid = this.class_lists[i].class_id;
          a[gid] = {from: 1, to: this.class_lists[i].students.length, exclude: []}
        }

        //a
        console.log('array');
        console.log(a);
        this.postListTo(a)

      },

      getAuthKey() {
        return localStorage.getItem('result.data.body.key');
      },

      //postArray
      async postListTo(a) {
        const result = this.getAuthKey();
        console.log('key', result);
        const res = await postList(a);
        console.log('home', res);
        console.log('seed', res.data.body.seed);
        localStorage.setItem('seed', res.data.body.seed);
        localStorage.setItem('res.data.body.id', res.data.body.id);
      }
        // localStorage.setItem("lists", JSON.stringify(this.lists));
        // console.log('', JSON.parse(localStorage.getItem('lists')));
        //
        // // 
        // this.$router.push({name: 'attendance'});
      },

    mounted() {
      // 
      // let lists = JSON.parse(localStorage.getItem('lists'));
      // console.log('mounted',lists);
      // if (lists) {
      //   this.lists = lists;
      // }
      // console.log('mounted',this.lists);
      // 
      // window.onbeforeunload = function (e) {
      //   var storage = window.localStorage;
      //   storage.clear()
      // }
    }

  }
</script>

<style>

  .sign-box {
    width: 1000px;
    margin: 150px auto;
    text-align: center;
  }

  .sign-box h4 {
    font-weight: 400;
    font-size: 14px;
    height: 54px;
  }

  .sign-bt {
    height: 22px;
    color: #fff;
    line-height: 22px;
    width: 100px;
    background: #1aad19;
    border-radius: 5px;
    box-shadow: 0 2px 2px #9EFABC;
    font-size: 14px;
    margin-bottom: 20px;
  }

  .change {
    margin-bottom: 10px;
  }

  .sign-box .change i {
    font-size: 20px;
    color: #8d8a8a;
    margin: 0 5px;
    box-shadow: 0 0 0 rgba(255, 255, 255, 0.24), 0 1px 6px rgba(0, 0, 0, 0.45);
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    cursor: pointer;
  }

  .sign-box .msg input {
    height: 24px;
    margin-top: 10px;
    text-indent: 10px;
    border-radius: 5px;
    box-shadow: 0 1px 0 rgba(255, 255, 255, 0.24), 0 1px 6px rgba(0, 0, 0, 0.45) inset;
    font-size: 12px;
  }


</style>



